<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>My Coupons</h1>
      <div class="chartWrap">
        <span class="bag" v-if="indexData.shoppingCartNum > 0">
          <em class="font10"> {{ indexData.shoppingCartNum }} </em>
        </span>
        <a
          href="javascript:void(0)"
          @click="$router.push('/cart')"
          class="iconfont cart"
        >
          &#xe669;
        </a>
      </div>
    </section>
    <vx-pull-refresh  @refresh="onRefresh" >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="The end"
        @load="onLoad"
      >
        <div class="couponsWrap">
          <ul>
            <li v-for="(item, index) in result" :key="index">
              <div class="coupon_left">
                <span
                  >ADE <b>{{ (item.result / 100).toFixed(0) }}</b
                  >.{{ (item.result / 100).toFixed(2).split(".")[1] }}</span
                >
                <p>Order AED {{ (item.condition / 100).toFixed(2) }}+</p>
              </div>
              <div class="coupon_right">
                <span>{{ StringToDate(item.beginTime) }}-{{ StringToDate(item.endTime) }}</span>
                <p>{{ item.name }}</p>
              </div>
            </li>
          </ul>
        </div>
      </van-list>
    </vx-pull-refresh>
  </div>
</template>

<script>
import { myCoupons } from "@/api/user";
import { mapGetters } from "vuex";
export default {
  name: "coupons",
  data() {
    return {
      loading: false,
      finished: false,
      isLoading: true,
      value: "4",
      result: [],
      query: {}
    };
  },
  computed: {
    ...mapGetters(["indexData"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.query = Object.assign(this.query, { pageNo: 1, pageSize: 10 });
      myCoupons(this.query)
        .then(res => {
          this.result = res.data.result.result;
          this.isLoading = false;
          if (res.data.result.totalNum <= this.result.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.isLoading = false;
          this.loading = false;
        });
    },
    onLoad() {
      if (this.isLoading) {
        this.loading = false;
        return false;
      }
      this.query.pageNo++;
      let query = this.query;
      myCoupons(this.query)
        .then(res => {
          this.result = this.result.concat(res.data.result.result);
           this.loading = false;
          if (res.data.result.totalNum <= this.result.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.loading = false;
        });
    },

     onRefresh(done) {
      setTimeout(() => {
        this.init();
    
        done()
      }, 500);
      
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    StringToDate(DateStr)  {   
      var converted = Date.parse(DateStr);  
      var myDate = new Date(converted);  
      if (isNaN(myDate))  
        {   
            //var delimCahar = DateStr.indexOf('/')!=-1?'/':'-';  
            var arys= DateStr.split('-');  
            myDate = new Date(arys[0],--arys[1],arys[2]);  
        }  
        var now = myDate;
        var year = now.getFullYear();       //年
        var month = now.getMonth() + 1;     //月
        var day = now.getDate();            //日
       
        var hh = now.getHours();            //时
        var mm = now.getMinutes();          //分
        var ss = now.getSeconds();          //秒

        if(day < 10)
            clock += "0";
           
        var clock = day + "/";

        if(month < 10)
            clock += "0";
       
        clock += month + "/";

        clock += year + " ";
       
       
        if(hh < 10)
            clock += "0";
           
        clock += hh + ":";

        if (mm < 10)
          clock += '0'; 

        clock += mm + ":";
 
        if (ss < 10)
          clock += '0';

        clock += ss;
        return(clock); 

    } 
  }
};
</script>

<style lang="scss" scoped>
.couponsWrap {
  ul {
    border-top: solid 10px #f6f6f6;
    padding-top: 13px;
  }
  li {
    height: 98px;
    width: 100%;
    background: url("../common/images/coupons.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin-bottom: 8px;
    display: -webkit-box;
  }
}

.coupon_left {
  height: 100%;
  width: 39%;
  position: relative;
  color: #ffffff;
  font-size: 14px;
  span {
    position: absolute;
    padding-top: 16%;
    right: 10px;
  }
  b {
    font-size: 25px;
  }
  p {
    font-size: 12px;
    position: absolute;
    right: 10px;
    bottom: 15px;
  }
}
.coupon_right {
  width: 61%;
  height: 100%;
  position: relative;
  display:flex;align-items: center;
  span {
    display: block;
    color: #999999;
    font-size: 11px;
    transform: scale(0.9);
    position: relative;
    left: -6px;
  }
  p {
    transform: scale(0.83);
    position: absolute;
    padding-left: 3px;
    font-size: 10px;
    bottom: 5px;
    color: #666666;
  }
}
</style>



